<template>
  <div id="app" class="app">
    <button @click="showDialog">弹窗</button>
    <button @click="showTips">小提示</button>
    <button @click="showLoading">加载</button>
  </div>
</template>

<script>
export default {
  name: 'app',
  data () {
    return {}
  },
  created() {

  },
  methods: {
    showDialog() {
      this.$layer.dialog({
        content: '弹窗',
        btn: [{
          text: '取消',
          cancel: true
        }, {
          text: '确定'
        }]
      })
    },
    showTips() {
      this.$layer.tips({
        content: '请输入姓名'
      }).then(time => {
        console.log(time)
      })
    },
    showLoading() {
      this.$layer.tips({
        type: 'loading',
        content: '加载中...'
      })
      // setTimeout(() => {
      //   this.$layer.close()
      // }, 5000)
    }
  }
}
</script>


<style lang="less" rel="stylesheet/less">
.app {
  .tips{
    position: absolute; top: 50%; left: 50%; transform: translate(-50%, -50%);
    text-align: center;
    max-width: 200px; padding: 10px 20px; line-height: 20px; font-size: 15px; color: #fff;
    background: rgba(0,0,0,.7); border-radius: 8px;
  }
  .loading-wrapper{
    position: absolute; top: 50%; left: 50%; transform: translate(-50%, -50%);
    background: rgba(0,0,0,.7); border-radius: 8px; padding: 15px;
    text-align: center;
    border-radius: 10px;
    .loading{
      width: 20px; height: 20px; margin: 0 auto;
      background:url('data:image/gif;base64,') no-repeat;
      background-size: 100% 100%;
    }
    .text{
       margin-top: 5px; font-size: 14px; color: #fff;
    }
  }
}
</style>



